$color_brown: #BDB175;


@import "common";
.page_index {
  opacity: 1;
  text-align: left;



  span.brown {
    color: $color_brown; }
  span.theme {
    color: $color_theme; }

  .container.flex {
    height: 100vh; }

  .col_left,.col_right {
    h1,h2,h3,h4,h5,h6,p {
      text-align: left; } }

  section {
    min-height: 900px;
    .section_title {
      margin-bottom: $gutter_width*3;
      letter-spacing: 3px;
      font-size: 30px; }
    .section_feature {
      margin-bottom: grid_sz(0,1);
      padding: 0px grid_sz(0,0.5);
      letter-spacing: 3px; }
    .section_para {
      margin-bottom: grid_sz(0,3);
      padding: 0px grid_sz(0,0.5); }
    .btn-underline {
      position: relative;
      height: grid_sz(0,2);
      background-color: transparent;
      color: $color_brown;
      font-size: 18px;
      letter-spacing: 4px;
      border-bottom: solid 2px $color_brown;
      @include flex_center;
      &:after {
        content: ">";
        display: block;
        position: absolute;
        right: -40px;
        transform: scaleX(0.8); }
      &.white {
        color: $color_white;
        border-color: $color_white; } } }


  .page_index_info {
    min-height: 0px;
    display: flex;
    flex-direction: row;


    .left,.right {
      // border: solid 1px
      position: relative;
      @include trans;
      @include flex_center;
      * {
        color: $color_white; }
      &:before {
        @include pseudo_ab_full; }

      .infos {
        padding: grid_sz(0,3) grid_sz(0,2);
        border: solid 1px white;
        max-width: grid_sz(6,6);
        // display: inline-block

        @include trans; }
      p {
        margin-top: grid_sz(0,2);
        overflow: hidden;
        max-height: 0px;
        max-width: {}
        opacity: 0;
        @include trans;
        transition-delay: 0.5s; }

      width: calc(50% + #{grid_sz(5,5)});
      &:hover {
        width: calc(50% + #{grid_sz(8,7)});
        .infos {
          border-color: transparent;
          p {
            max-height: 400px;
            opacity: 1; } } } }


    .left {
      background-image: url("../img/index/1b.jpg");
      // width: calc(50vw - 50% + #{grid_sz(5,5)})
      height: grid_sz(0,17);

      .infos {
        float: right; }

      &:before {
        background-color: rgba($color_theme,0.8); } }
    .right {
      background-image: url("../img/index/1c.jpg");
      // width: calc(50vw - 50% + #{grid_sz(8,7)})
      height: grid_sz(0,18);

      .infos {
        float: left; }
      &:before {
        background-color: rgba($color_brown,0.8); } } }


  //-----------------------------------------------
  .page_index_main {
    $news_height: grid_sz(0,12.5);
    background-image: url("../img/index/1a.jpg");
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    background-position: center 0px;

    .play_btn {
      border-radius: 50%;
      background-color: $color_theme;
      color: $color_white;
      @include flex_center;
      @include size(50px); }
    .row {
      position: relative; }
    .index_slogan_area {
      height: 100%;
      @include flex_center;
      flex-direction: column;
      .title_span {
        display: inline-block;
        width: grid_sz(1,2); } }

    h1 {
      letter-spacing: 15px;
      margin-bottom: 0px;
      margin-top: 0px;
      animation: lspace_in 2s 1 both;
      text-align: center;
      font-size: 48px;
      span {
        font-weight: 300; } }

    @keyframes lspace_in {
      0% {
        letter-spacing: 10px;
        opacity: 0; }
      100% {
        letter-spacing: 15px;
        opacity: 1; } }

    h3.page_header_eng {
      margin-top: 10px;
      font-size: 30px;
      letter-spacing: 2px;
      animation: lspace_in2 2s 0.5s 1 both;
      text-align: center;
      font-weight: 300;
      margin-bottom: grid_sz(0,2); }

    @keyframes lspace_in2 {
      0% {
        letter-spacing: 0px;
        opacity: 0; }
      100% {
        letter-spacing: 2px;
        opacity: 1; } }


    .news_container {
      position: absolute;
      bottom: 0px;
      width: grid_sz(10,9);
      height: $news_height;
      @include box_shadow; }


    .news {
      display: flex;
      flex-direction: row;
      background-color: $color_theme;
      height: 100%;

      opacity: 1;

      .col_infos,.col_img {
        height: 100%;
        position: relative; }

      .col_infos {
        width: grid_sz(6,6);
        padding: grid_sz(0,1);
        display: flex;
        flex-direction: column;
        justify-content: space-between; }

      .col_img {
        width: grid_sz(4,3); }

      .col_infos .btns {
        @include flex_split("lr");
        width: 100%;
        z-index: 10;

 }        // height: 200px
      h4,h5,p {
        color: $color_white;
        margin: 0;
        font-weight: 300; }
      h5 {
        margin-bottom: $gutter_width; }
      .cover_img {
        background-image: url("../img/homepage/Home2.jpg");
        background-position: center center;
        background-size: auto 110%;
        height: 200px+$gutter_width*3;
        margin-right: 0px;
 } } }        // transition:  background-image 0.5s

  //-----------------------------------------------

  .page_index_grow {
    // background-image: url("../img/index/2c.jpg")

    .cover {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0; } }



  //-----------------------------------------------

  .page_index_live {
    background-image: url("../img/index/3a.jpg");
    @include bg_block('before','left',rgba($color_theme,0.9),grid_sz(1,2));
    * {
      font-weight: 300; }
    .col_left {
      h3,h4,p {
        color: white; } }
    .btn {
      margin-top: $gutter_width*2; }

    .percent {
      position: absolute;
      right: 0;
      left: auto;
      bottom: grid_sz(0,5); } }

  //----------------------------------------------
  .page_index_accurate {
    background-image: url("../img/homepage/Home5.jpg");
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 120vh;


    .section_title,.section_para {
      color: $color_white; }

    .col_right {
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,14); }

    .btn {
      margin-top: $gutter_width*2; }


    @include bg_block('before','right',$color_theme);


    .col-sm-6 {
      padding-left: 1.5*$gutter_width;
      padding-right: 1.5*$gutter_width;
      text-align: center;
      h3,p {
        color: $color_white; } }

    .btn {
      margin-bottom: 0; }

    .acc_sm_pic {
      // background-color: $color_theme
      border: none;
      width: 50%;
      height: 100%;
      cursor: pointer;

      @include range_width(0px,800px) {
        width: 100%;
        height: 100px; }

      &:hover {
        &:before {
          opacity: 1; } }
      &:before {
        @include pseudo_ab_full;
        @include ab_center;
        background-color: rgba($color_theme,0.5);
        opacity: 0;
        transition: 0.5s;
        z-index: 2; }
      &:after {
        @include pseudo_ab_full;
        @include ab_center;
        background-size: cover;
        z-index: 1; }
      h4 {
        color: $color_white;
        font-size: 20px;
        letter-spacing: 3px;
        @include ab_center;
        z-index: 3; } }

    .acc_sm_pic[data-type="1"]:after {
      background-image: url("../img/homepage/Home6-1.jpg"); }
    .acc_sm_pic[data-type="2"]:after {
      background-image: url("../img/homepage/Home6-4.jpg"); }
    .acc_sm_pic[data-type="3"]:after {
      background-image: url("../img/homepage/Home6-2.jpg"); }
    .acc_sm_pic[data-type="4"]:after {
      background-image: url("../img/homepage/Home6-3.jpg"); }


    .type_container {
      position: absolute;
      bottom: 0px;
      height: grid_sz(0,14);
      flex-direction: column;
      z-index: 100;


      .row {
        display: flex;
        @include size(grid_sz(8,8),50%);
        &:nth-child(1) {
          left: grid_sz(4,4); }
        &:nth-child(2) {
          left: grid_sz(2,2); }

        @include range_width(0px,800px) {
          flex-direction: column;
          height: grid_sz(0,28);
          width: 100%;

          &:nth-child(1) {
            left: 0; }
          &:nth-child(2) {
            left: 0; } } } } }

  //-------------------------
  .page_index_service {
    .container.flex.full {
      .col_left {
        background-image: url("../img/index/4f.jpg"); }
      .col_right {} } }

  //--------------------------
  .page_index_accurate {}

  //
  .page_index_compaign {
    h1 {
      font-size: grid_sz(0,3);
      margin-bottom: grid_sz(1,1);
      color: #BBB288;
      letter-spacing: grid_sz(0,2); }
    h3 {
      font-size: 30px;
      margin-bottom: grid_sz(0,2); }
    background-image: url("../img/index/6a.jpg");
    background-size: cover;
    text-align: center; }

  //------------------------------------------------------
  .page_index_sponsor {
    min-height: initial;
    background-color: $color_grey_bg;
    .item {
      width: 25%; }

    .container {
      height: auto;
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,2);
      justify-content: space-around;
      margin: auto; }

    .sponsorlogo {
      width: grid_sz(2,2);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      // padding-left: grid_sz(0,1)
      // padding-right: grid_sz(0,1)

      mix-blend-mode: darken; } }

  @include range_width(0px, 800px) {

    section {
      min-height: 100vh;
      height: auto; }

    .percent {
      font-size: 100px;
      &:after {
        font-size: 60px; } }

    .page_index_main {
      min-height: 150vh;
      background-size: cover;

      .index_slogan_area {
        height: 70vh; }
      .news_container {
        width: 100%;
        height: auto;
        .news {
          flex-direction: column;
          height: auto;
          .col_img {
            order: -1;
            width: 100%;
            height: 200px; }
          .col_infos {
            width: 100%;
            height: auto;
            min-height: 350px; } } } }


    .page_index_grow {
      .percent {
        right: grid_sz(0,2); }


      .col_left {
        display: none; } }

    .page_index_live {
      .percent {
        bottom: 0px;
        right: grid_sz(0,2); }
      .col_right {
        min-height: initial;
        height: 0px; } }


    .page_index_accurate {
      min-height: 130vh;
      display: block;
      align-items: flex-start;

      .container.flex {
        align-items: flex-start;
        .col_left {
          display: none; }
        .col_right {
          display: block;
          padding-top: grid_sz(0,3);
          padding-bottom: grid_sz(0,3);
          .section_title {
            margin-top: 0px; } } } } } }







